<script>
  import { onMount } from "svelte";
  import parallax from "./parallax";

  function startParallax() {
    let $ = document.querySelector.bind(document);

    let blob1 = $("#blob-1");
    let blob2 = $("#blob-2");
    let blob3 = $("#blob-3");
    let blob4 = $("#blob-4");
    let blob5 = $("#blob-5");
    let blob6 = $("#blob-6");
    let blob7 = $("#blob-7");
    let blob8 = $("#blob-8");
    let blob9 = $("#blob-9");

    const xmultiplier = window.innerWidth / 960;
    const overlapPixels = xmultiplier * 30;
    const initialOffset = window.pageYOffset;

    let service1 = $("#service1").getBoundingClientRect();
    const service1YOffset = service1.top + initialOffset;
    const service1XOffset = service1.left + overlapPixels;

    let service2 = $("#service2").getBoundingClientRect();
    const service2YOffset = service2.top + initialOffset + 60;
    const service2XOffset = service2.right - overlapPixels;

    let service3 = $("#service3").getBoundingClientRect();
    const service3YOffset = service3.top + initialOffset + 120;
    const service3XOffset = service3.left + overlapPixels;

    let service4 = $("#service4").getBoundingClientRect();
    const service4YOffset = service4.top + initialOffset + 180;
    const service4XOffset = service4.right - overlapPixels;

    let service5 = $("#service5").getBoundingClientRect();
    const service5YOffset = service5.top + initialOffset + 240;
    const service5XOffset = service5.left + overlapPixels;

    let service6 = $("#service6").getBoundingClientRect();
    const service6YOffset = service6.top + initialOffset + 300;
    const service6XOffset = service6.right - overlapPixels;

    parallax(
      (lastScrollY, prefix, pos, setTickingFalse) => {
        let relativeY = lastScrollY / 4000;
        // big blue top corner
        prefix(
          blob1.style,
          "Transform",
          "translate3d(" +
            ((960 * xmultiplier).toFixed() - blob1.width).toFixed() +
            "px," +
            pos(-100, -200, relativeY, 0) +
            "px, 0)"
        );
        // big red
        prefix(
          blob2.style,
          "Transform",
          "translate3d(" +
            300 * xmultiplier +
            "px," +
            pos(604, -140, relativeY, 0) +
            "px, 0)"
        );

        // first small blue
        prefix(
          blob3.style,
          "Transform",
          "translate3d(" +
            268 * xmultiplier +
            "px," +
            pos(44, -3500, relativeY, 0) +
            "px, 0)"
        );

        // service 1
        prefix(
          blob4.style,
          "Transform",
          "translate3d(" +
            (service1XOffset - blob4.getBoundingClientRect().width).toFixed() +
            "px," +
            pos(service1YOffset, -400, relativeY, 0) +
            "px, 0)"
        );

        // service 2
        prefix(
          blob5.style,
          "Transform",
          "translate3d(" +
            service2XOffset +
            "px," +
            pos(service2YOffset, -400, relativeY, 0) +
            "px, 0)"
        );

        // service 3
        prefix(
          blob6.style,
          "Transform",
          "translate3d(" +
            (service3XOffset - blob6.getBoundingClientRect().width).toFixed() +
            "px," +
            pos(service3YOffset, -400, relativeY, 0) +
            "px, 0)"
        );

        // service 4
        prefix(
          blob7.style,
          "Transform",
          "translate3d(" +
            service4XOffset +
            "px," +
            pos(service4YOffset, -400, relativeY, 0) +
            "px, 0)"
        );

        // service 5
        prefix(
          blob8.style,
          "Transform",
          "translate3d(" +
            (service5XOffset - blob8.getBoundingClientRect().width).toFixed() +
            "px," +
            pos(service5YOffset, -400, relativeY, 0) +
            "px, 0)"
        );

        // service 6
        prefix(
          blob9.style,
          "Transform",
          "translate3d(" +
            service6XOffset +
            "px," +
            pos(service6YOffset, -400, relativeY, 0) +
            "px, 0)"
        );
        setTickingFalse();
      },
      function() {
        blob1.classList.add("force-show-sm");
        blob2.classList.add("force-show");
        blob3.classList.add("force-show");
        blob4.classList.add("force-show");
        blob5.classList.add("force-show");
        blob6.classList.add("force-show");
        blob7.classList.add("force-show");
        blob8.classList.add("force-show");
        blob9.classList.add("force-show");
      },
      startParallax
    );
  }

  onMount(async () => {
    startParallax();
  });

  import Nav from "../components/Nav.svelte";
  import Footer from "../components/Footer.svelte";
</script>

